<script>
	export default {
		data() {
			return {
				currentCategory: 'all',
				showFilter: false,
				selectedDuration: '',
				selectedHeat: '',
				hasMore: true,
				page: 1,
				categoryList: [
					{ icon: 'üìÇ', name: 'ÂÖ®ÈÉ®', type: 'all' },
					{ icon: 'üìö', name: 'ÊúâÂ£∞‰π¶', type: 'book' },
					{ icon: 'üéµ', name: 'Èü≥‰πê', type: 'music' },
					{ icon: 'üéôÔ∏è', name: 'Êí≠ÂÆ¢', type: 'podcast' },
					{ icon: 'üì∞', name: 'Êñ∞Èóª', type: 'news' },
					{ icon: 'üßò', name: 'ÂÜ•ÊÉ≥', type: 'meditation' },
					{ icon: 'üèÉ', name: 'ËøêÂä®', type: 'sport' },
					{ icon: 'üë∂', name: 'ÂÑøÁ´•', type: 'children' },
					{ icon: 'üé≠', name: 'ÊàèÊõ≤', type: 'opera' }
				],
				durationOptions: [
					{ label: 'ÂÖ®ÈÉ®', value: '' },
					{ label: 'Áü≠ÁØá(< 30ÂàÜÈíü)', value: 'short' },
					{ label: '‰∏≠ÁØá(30-60ÂàÜÈíü)', value: 'medium' },
					{ label: 'ÈïøÁØá(> 60ÂàÜÈíü)', value: 'long' }
				],
				heatOptions: [
					{ label: 'ÂÖ®ÈÉ®', value: '' },
					{ label: 'ÊúÄÊñ∞', value: 'latest' },
					{ label: 'ÊúÄÁÉ≠', value: 'hottest' },
					{ label: 'ËØÑÂàÜÊúÄÈ´ò', value: 'highest' }
				],
				allContentList: [
					{
						id: 1,
						title: '‰∏â‰ΩìÔºöÈªëÊöóÊ£ÆÊûó',
						author: 'ÂàòÊÖàÊ¨£',
						cover: '/static/audio1.jpg',
						duration: '15:30:45',
						category: 'book',
						views: '12.5‰∏á',
						likes: '8.2ÂçÉ',
						isFavorited: false
					},
					{
						id: 2,
						title: 'Â§úÂê¨ÊïÖ‰∫ã',
						author: 'Â§úÂê¨‰∏ªÊí≠',
						cover: '/static/audio2.jpg',
						duration: '25:20',
						category: 'podcast',
						views: '8.9‰∏á',
						likes: '5.6ÂçÉ',
						isFavorited: true
					},
					{
						id: 3,
						title: 'ËΩªÊùæÁàµÂ£´‰πê',
						author: 'ÁàµÂ£´‰πêÂõ¢',
						cover: '/static/audio3.jpg',
						duration: '1:45:30',
						category: 'music',
						views: '6.3‰∏á',
						likes: '4.1ÂçÉ',
						isFavorited: false
					},
					{
						id: 4,
						title: 'Áù°ÂâçÂÜ•ÊÉ≥',
						author: 'ÂÜ•ÊÉ≥ÂØºÂ∏à',
						cover: '/static/audio4.jpg',
						duration: '18:00',
						category: 'meditation',
						views: '15.2‰∏á',
						likes: '12.8ÂçÉ',
						isFavorited: true
					},
					{
						id: 5,
						title: '‰ªäÊó•Êñ∞ÈóªÊëòË¶Å',
						author: 'Êñ∞ÈóªÊí≠Êä•Âëò',
						cover: '/static/audio5.jpg',
						duration: '12:30',
						category: 'news',
						views: '23.6‰∏á',
						likes: '3.2ÂçÉ',
						isFavorited: false
					},
					{
						id: 6,
						title: 'ÂÑøÁ´•ÊïÖ‰∫ãÂ§ßÂÖ®',
						author: 'ÊïÖ‰∫ãÂßêÂßê',
						cover: '/static/audio6.jpg',
						duration: '35:45',
						category: 'children',
						views: '18.7‰∏á',
						likes: '15.3ÂçÉ',
						isFavorited: false
					}
				]
			}
		},
		computed: {
			filteredList() {
				let list = this.allContentList;
				
				// ÊåâÂàÜÁ±ªÁ≠õÈÄâ
				if (this.currentCategory !== 'all') {
					list = list.filter(item => item.category === this.currentCategory);
				}
				
				// ÊåâÊó∂ÈïøÁ≠õÈÄâ
				if (this.selectedDuration) {
					list = list.filter(item => {
						const duration = this.parseDuration(item.duration);
						switch (this.selectedDuration) {
							case 'short':
								return duration < 30;
							case 'medium':
								return duration >= 30 && duration <= 60;
							case 'long':
								return duration > 60;
							default:
								return true;
						}
					});
				}
				
				return list;
			}
		},
		onLoad() {
			this.loadContent();
		},
		methods: {
			switchCategory(type) {
				this.currentCategory = type;
			},
			getCategoryName(type) {
				const category = this.categoryList.find(item => item.type === type);
				return category ? category.name : '';
			},
			showFilterPopup() {
				this.showFilter = true;
			},
			hideFilterPopup() {
				this.showFilter = false;
			},
			showSortPopup() {
				uni.showActionSheet({
					itemList: ['ÊúÄÊñ∞ÂèëÂ∏É', 'ÊúÄÂ§öÊí≠Êîæ', 'ÊúÄÂ§öÊî∂Ëóè', 'Êó∂ÈïøÊúÄÁü≠', 'Êó∂ÈïøÊúÄÈïø'],
					success: (res) => {
						console.log('ÈÄâÊã©‰∫ÜÊéíÂ∫èÊñπÂºè:', res.tapIndex);
						this.sortContent(res.tapIndex);
					}
				});
			},
			selectDuration(value) {
				this.selectedDuration = value;
			},
			selectHeat(value) {
				this.selectedHeat = value;
			},
			resetFilter() {
				this.selectedDuration = '';
				this.selectedHeat = '';
			},
			applyFilter() {
				this.hideFilterPopup();
				// Â∫îÁî®Á≠õÈÄâÊù°‰ª∂
			},
			toggleFavorite(item) {
				item.isFavorited = !item.isFavorited;
				uni.showToast({
					title: item.isFavorited ? 'Â∑≤Êî∂Ëóè' : 'Â∑≤ÂèñÊ∂àÊî∂Ëóè',
					icon: 'none'
				});
			},
			playContent(item) {
				uni.showToast({
					title: `Ê≠£Âú®Êí≠Êîæ: ${item.title}`,
					icon: 'none'
				});
			},
			parseDuration(duration) {
				// Ëß£ÊûêÊó∂ÈïøÂ≠óÁ¨¶‰∏≤ÔºåËøîÂõûÂàÜÈíüÊï∞
				const parts = duration.split(':');
				if (parts.length === 3) {
					// Ê†ºÂºèÔºöÂ∞èÊó∂:ÂàÜÈíü:Áßí
					return parseInt(parts[0]) * 60 + parseInt(parts[1]);
				} else if (parts.length === 2) {
					// Ê†ºÂºèÔºöÂàÜÈíü:Áßí
					return parseInt(parts[0]);
				}
				return 0;
			},
			sortContent(sortType) {
				// ÂÆûÁé∞ÊéíÂ∫èÈÄªËæë
				console.log('ÊéíÂ∫èÁ±ªÂûã:', sortType);
				uni.showToast({
					title: 'ÊéíÂ∫èÂÆåÊàê',
					icon: 'none'
				});
			},
			loadContent() {
				// Ê®°ÊãüÂä†ËΩΩÂÜÖÂÆπ
				console.log('Âä†ËΩΩÂàÜÁ±ªÂÜÖÂÆπ');
			},
			loadMore() {
				if (!this.hasMore) return;
				
				// Ê®°ÊãüÂä†ËΩΩÊõ¥Â§ö
				setTimeout(() => {
					this.page++;
					console.log('Âä†ËΩΩÊõ¥Â§öÂÜÖÂÆπ, È°µÁ†Å:', this.page);
					
					if (this.page >= 3) {
						this.hasMore = false;
					}
				}, 1000);
			}
		}
	}
</script>

